//引入和安装路由
import vueRouter from "vue-router";
import Vue from "vue";
Vue.use(vueRouter);

//配置路由
import Category from "../pages/Category";
import GoodDetail from "../pages/GoodDetail";
import GoodList from "../pages/GoodList";
import ErrorPage from "../pages/ErrorPage";
import Home from "../pages/Home";
import LoginAct from "../pages/LoginAct";
import LoginMsg from "../pages/LoginMsg";
import Mine from "../pages/Mine";
import ShopCart from "../pages/ShopCart";
import SwiperClick from "../pages/SwiperClick";

let routes=
[
    {path:"/category",component:Category},
    {path:"/gooddetail",component:GoodDetail},
    {path:"/errorpage",component:ErrorPage},
    {path:"/goodlist",component:GoodList},
    {path:"/home",component:Home},
    {path:"/loginact",component:LoginAct},
    {path:"/loginmsg",component:LoginMsg},
    {path:"/mine",component:Mine},
    {path:"/shopcart",component:ShopCart},
    {path:"/swiperclick",component:SwiperClick,children:
        [
            {name:"swiper1",path:":mid",component:SwiperClick},
            {name:"swiper2",path:":mid",component:SwiperClick},
            {name:"swiper3",path:":mid",component:SwiperClick},
            {name:"swiper4",path:":mid",component:SwiperClick},
            {name:"swiper5",path:":mid",component:SwiperClick},
            {name:"swiper6",path:":mid",component:SwiperClick}
        ]
    },
    {path:"/",redirect:"/home"},
    {path:"*",redirect:"/errorpage"}
];

//实例
let router=new vueRouter(
{
    routes,
    mode:"history" //默认hash模式
});

export default router;